import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'equipamento-dados',   templateUrl: 'equipamento_dados.component.html' }) export class EquipamentoDadosComponent implements OnInit {       displayMode: EquipamentoDadosDisplayModeEnum;     displayModeEnum = EquipamentoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = EquipamentoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = EquipamentoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = EquipamentoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum EquipamentoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


