import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Equipamento } from '../../../modelo/Equipamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'equipamento-dados-details',   templateUrl: 'equipamento_dadosDetails.component.html' }) export class EquipamentoDadosDetailsComponent implements OnInit {    equipamento: Equipamento;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.equipamento.Equipamento";                 let id = +params['id'];         this.equipamento = new Equipamento();         this.equipamento.cd_equipamento = id;         o.objetoJson = this.equipamento;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a equipamento. '; }      this.equipamento = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


